import React from "react";
import "./components/css/App.css";
import Contact from "./components/home/contact";
import Login from "./components/home/login";
import SignUp from "./components/home/signup";
import Dashboard from "./components/dashboard/dashboard";
import Index from "./components/home/index";
import Profile from './components/dashboard/profile';
import Settings from './components/dashboard/settings';
import Emailsetting from './components/dashboard/emailsetting';
import Passwordsecurity from './components/dashboard/security';
import Paymentsetting from './components/dashboard/paymentsetting';
import About from './components/dashboard/about';
import { AnimatePresence } from "framer-motion";
import OurTeam from './components/dashboard/ourteam';
import PrivacyPolicy from './components/dashboard/privacypolicy';
import Cardlink from './components/dashboard/cardlink';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

function App() {
  return (
    <Router>
      <div className="App">
        <AnimatePresence exitBeforeEnter>
          <Switch>
            <Route path="/" exact component={Index} />
            <Route path="/contact" exact component={Contact} />
            <Route path="/login" exact component={Login} />
            <Route path="/signup" exact component={SignUp} />
            <Route path="/dashboard" exact component={Dashboard} />
            <Route path="/profile" exact component={Profile} />
            <Route path="/settings" exact component={Settings} />
            <Route path="/emailsettings" exact component={Emailsetting} />
            <Route path="/password" exact component={Passwordsecurity} />
            <Route path="/payments" exact component={Paymentsetting} />
            <Route path="/cardlink" exact component={Cardlink} />
            <Route path="/about" exact component={About} />
            <Route path="/ourteam" exact component={OurTeam} />
            <Route path="/privacypolicy" exact component={PrivacyPolicy} />
          </Switch>
        </AnimatePresence>
      </div>
    </Router>
  );
}

export default App;
