import React, { useState } from "react";
import Cards from "react-credit-cards";
import { Link } from "react-router-dom";
import "../css/cardlink.css";
import "react-credit-cards/es/styles-compiled.css";

export default function Cardlink() {
  const [number, setNumber] = useState("");
  const [name, setName] = useState("");
  const [expiry, setExpiry] = useState("");
  const [cvc, setCvc] = useState("");
  const [focus, setFocus] = useState("");

  return (
    <div className="cardlink-main-container">
        
      <form className="form-main">
          <h1 className="link-brand"><span className="brandname">Demo</span>lance</h1>
          <p className="enter">Please fill the form to proceed.</p>
        <Cards
          number={number}
          name={name}
          xpiry={expiry}
          cvc={cvc}
          focused={focus}
        />
        <input
          className="inputbox-card"
          type="tel"
          name="number"
          id="number"
          placeholder="Card Number"
          value={number}
          onChange={(e) => setNumber(e.target.value)}
          onFocus={(e) => setFocus(e.target.name)}
        />{" "}
        <br />
        <input
          className="inputbox-card"
          type="text"
          name="name"
          id="name"
          placeholder="Name"
          value={name}
          onChange={(e) => setName(e.target.value)}
          onFocus={(e) => setFocus(e.target.name)}
        />{" "}
        <br />
        <input
          className="inputbox-card"
          type="text"
          name="expiry"
          id="expiry"
          placeholder="MM/YY Expiry"
          value={expiry}
          onChange={(e) => setExpiry(e.target.value)}
          onFocus={(e) => setFocus(e.target.name)}
        />{" "}
        <br />
        <input
          className="inputbox-card"
          type="tel"
          name="cvc"
          id="cvc"
          placeholder="CVC"
          value={cvc}
          onChange={(e) => setCvc(e.target.value)}
          onFocus={(e) => setFocus(e.target.name)}
        />{" "}
        <br />
        <button className="pay">Proceed</button>
      </form>
    </div>
  );
}
